.buttonList {
  position: fixed;
  width: 50px;
  z-index: 999;
  margin: 0;
  top: 40%;
  left: 0;
}

@media (max-width: 460px) {

  .buttonList {
    display: none;
  }
}

.buttonList {

  &Item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin: 20px 0;

    a:hover svg path {
      fill: #823eb7;
      stroke: #823eb7;
    }
  }
}
