.nav {
  background-color: transparent;
  -webkit-box-shadow: none;
          box-shadow: none;
  /**/
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;

  &Sticky {
    background-color: #fff;
    -webkit-box-shadow: 0 5px 20px -10px #000;
            box-shadow: 0 5px 20px -10px #000;
  }

  &Bar {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    min-height: 40px;
    padding: 10px 15px 10px 50px;
  }
}

@media (max-width: 460px) {

  .nav {

    &Bar {
      padding-left: 15px;
    }
  }
}

.nav {

  &Bar {

    &Logo {
      opacity: 0;
      /**/
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;

      &Sticky {
        opacity: 1;
      }
    }

    &List {
      /**/
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      margin: 0;
    }
  }
}

@media (max-width: 460px) {

  .nav {

    &Bar {

      &List {
        display: none;
      }
    }
  }
}

.nav {

  &Bar {

    &List {

      &Toggle {
        display: block;
      }

      &Item {
        margin: 0 10px;
      }
    }
  }
}

@media (max-width: 460px) {

  .nav {

    &Bar {

      &List {

        &Item {
          margin-top: 12px;
          margin-bottom: 20px;
        }
      }
    }
  }
}

.nav {

  &Bar {

    &List {

      &Item {

        &Link {
          text-transform: uppercase;
          letter-spacing: 3.5px;
          font-weight: 200;
          color: #fff;

          &Sticky {
            color: #333;
          }
        }
      }
    }

    &MobileToggle {
      display: none;
      -ms-flex-item-align: start;
          align-self: flex-start;
    }
  }
}

@media (max-width: 460px) {

  .nav {

    &Bar {

      &MobileToggle {
        display: block;
      }
    }
  }
}

.nav {

  &Bar {

    &MobileToggle {

      &Button {
        /* button module base*/
        color: #823eb7;
        background-color: #fff;
        border: 1px solid #823eb7;
        font-size: 13px;
        text-transform: uppercase;
        letter-spacing: 0.08em;
        padding: 6px 12px;
        margin-bottom: 0;
        line-height: 1.42857143;
        border-radius: 0;
        text-align: center;
        white-space: nowrap;
        vertical-align: middle;
        cursor: pointer;
        -webkit-transition: all 0.2s ease;
        transition: all 0.2s ease;
      }
    }
  }
}
