.topBorder {
  border-top: 1px solid #e4e4e4;
}

.container1 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-align: center;
  padding: 85px 20px;
  max-width: 800px;
  margin: 0 auto;
}

@media (max-width: 720px) {

  .container1 {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
}

.container1 {

  &Image {
    position: relative;
    overflow: hidden;
    /**/
    min-width: 250px;
    max-width: 300px;
    margin: 20px;

    picture img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      -o-object-fit: cover;
         object-fit: cover;
      -o-object-position: center center;
         object-position: center center;
      opacity: 1;
      -webkit-transition: opacity 500ms ease 0s;
      transition: opacity 500ms ease 0s;
    }
  }

  &Info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;

    &Lead {
      font-weight: 300;
      font-size: 21px;
      margin: 10px 0;
    }

    &Link {
      color: #fff;
      background-color: #823eb7;

      /* primary button*/
      display: inline-block;
      margin: 0;

      /* button module base round*/
      border-radius: 5px;

      /* button module base large*/
      padding: 20px;

      /* button module base*/
      border: 1px solid #823eb7;
      font-size: 13px;
      text-transform: uppercase;
      letter-spacing: 0.08em;
      margin-bottom: 0;
      line-height: 1.42857143;
      text-align: center;
      white-space: nowrap;
      vertical-align: middle;
      cursor: pointer;
      -webkit-transition: all 0.2s ease;
      transition: all 0.2s ease
    }

    &Link:visited path,
      &Link:visited svg {
      fill: #fff;
      stroke: #fff;
    }
  }
}
