@media (max-width: 460px) {

  .container1 {

    &Header {

      &SubHeader {

        &Primary {
          -webkit-box-orient: vertical;
          -webkit-box-direction: normal;
              -ms-flex-direction: column;
                  flex-direction: column;
        }
      }
    }
  }
}

.container1 {

  &Header {

    &FollowButtons {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      margin-bottom: 20px;

      &Item {
        margin: 0 5px;

        &FacebookFollow:hover,
          &FacebookFollow:visited {
          text-decoration: none;
          color: #fff;
          background-color: #3b5998;
        }

        &FacebookFollow {
          background-color: #3b5998;
          color: #fff;
          margin-top: -8px;
          padding: 0 6px;
          border: 1px solid #3b5998;
          border-radius: 3px;
          font-size: 12px;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-align: center;
              -ms-flex-align: center;
                  align-items: center;
        }

        &FacebookFollow:visited path,
          &FacebookFollow:visited svg {
          fill: #fff;
          stroke: #fff;
        }
      }
    }

    &Container1 {
      display: block;
      width: 100%;
      /**/
      position: relative;
      overflow: hidden;

      picture img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        -o-object-fit: cover;
           object-fit: cover;
        -o-object-position: center center;
           object-position: center center;
        opacity: 1;
        -webkit-transition: opacity 500ms ease 0s;
        transition: opacity 500ms ease 0s;
      }
    }
  }

  &Title {
    margin-top: 40px;
    padding-top: 20px;
    margin-bottom: 10px;
    padding-bottom: 10px;
    font-weight: 400;
    line-height: 1.1;
    border-top: 1px solid #e4e4e4;
    /**/
    text-align: center;
    font-size: 26px;
  }

  &List {
    margin: 20px;

    &Item {
      list-style-type: square;
    }
  }

  &Divider::before {
    letter-spacing: 0.6em;
    content: '...';
    display: inline-block;
    margin-left: 0.6em;
    position: relative;
  }

  &Divider {
    display: block;
    border: 0;
    text-align: center;
    overflow: visible;
  }

  &DividerButton {
    width: 100%;
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    margin: 40px 0;
    border-bottom: 1px solid #e4e4e4;

    button {
      position: absolute;
      top: -10px;
      padding: 0 10px;
      border: none;
      cursor: pointer;
      background-color: #fff;
      color: #545454;
    }
  }
}
